<template>
  <div>
    <v-btn router to="/" round color="black white--text">Home</v-btn>
    <v-layout row justify-center>
      <v-dialog v-model="editBox"
                fullscreen
                transition="dialog-bottom-transition"
                :overlay=false
                scrollable>
          <v-card>
             <v-toolbar style="flex: 0 0 auto;" dark class="primary">
          <v-btn icon @click.native="editBox = false" dark>
            <v-icon>close</v-icon>
          </v-btn>
          <v-toolbar-title>Settings</v-toolbar-title>
          <v-spacer></v-spacer>
          <v-toolbar-items>
            <v-btn dark flat @click.native="editBox = false">Save</v-btn>
            <v-menu bottom right offset-y>
              <v-btn slot="activator" dark icon>
                <v-icon>more_vert</v-icon>
              </v-btn>
              <v-list>
                <v-list-tile v-for="item in items" :key="item.title" @click="">
                  <v-list-tile-title>{{ item.title }}</v-list-tile-title>
                </v-list-tile>
              </v-list>
            </v-menu>
          </v-toolbar-items>
        </v-toolbar>
            <edit-communication :data="data">aaa</edit-communication>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" flat @click.native="editBox = false">Close</v-btn>
            </v-card-actions>
          </v-card>
      </v-dialog>
    </v-layout>
    <v-container text-xs-center>
      <v-layout>
        <v-flex>
          <vue-table @onEdit="showEditBox" :headers="headers" tableFor='communication'></vue-table>
        </v-flex>
      </v-layout>
    </v-container>
    <v-container class="mt-5">
      <v-layout row wrap>
        <v-flex xs4 offset-xs2 offset-md4 offset-lg4>
          <pi-chart chartFor='purpose' chartOf='communication' chartForUser='1' ></pi-chart>
        </v-flex>
      </v-layout>
    </v-container>
  </div>
</template>

<script>
  import PiChart from '../reusuable/PiChart'
  import VueTable from '../reusuable/VueTable'
  import EditCommunication from './EditCommunication'

  export default {
    components: {PiChart, VueTable, EditCommunication},
    data () {
      return {
        editBox: false,
        data: null,
        headers: [
          {text: 'Name', align: 'left', value: 'client_name'},
          {text: 'Medium', align: 'left', value: 'medium'},
          {text: 'Medium status', align: 'left', value: 'medium_status'},
          {text: 'Purpose', align: 'left', value: 'purpose'},
          {text: 'Remarks', align: 'left', value: 'remarks'},
          {text: 'Remainder Date', align: 'left', value: 'remainder_date'}
        ]
      }
    },
    methods: {
      showEditBox (data) {
        this.editBox = true
        this.data = data
      }
    }
  }
</script>



