<template>
  <div>
    <v-btn router to="/" round color="black white--text">Home</v-btn>
    <v-container text-xs-center>
      <v-layout>
        <v-flex>
          <vue-table :headers="headers" tableFor='lead'></vue-table>
        </v-flex>
      </v-layout>
    </v-container>
    <v-container class="mt-5">
      <v-layout row wrap>
        <v-flex xs4 offset-xs2 offset-md4 offset-lg4>
          <pi-chart chartFor='service' chartOf='lead' chartForUser='1' ></pi-chart>
        </v-flex>
      </v-layout>
    </v-container>
  </div>
</template>

<script>
  import PiChart from '../reusuable/PiChart'
  import VueTable from '../reusuable/VueTable'
  export default {
    components: {PiChart, VueTable},
    data () {
      return {
        headers: [
          {text: 'Service', align: 'center', value: 'service'},
          {text: 'Unit', align: 'center', value: 'unit'},
          {text: 'Status', align: 'center', value: 'status'},
          {text: 'Bulk', align: 'center', value: 'bulk'}
        ]
      }
    }
  }
</script>



