<template>
  <div>

    <h1> Table for {{ tableFor.charAt(0).toUpperCase() + tableFor.slice(1) }}s </h1> <br><br>

    <div v-if="selected.length > 0">
      <div class="text-xs-center">
        <v-btn round color="red" dark>
          <v-icon left dark>delete</v-icon>
          Delete
        </v-btn>
      </div>
    </div>

    <v-text-field
      @input="value => getDataFromApi(value, baseUrl)"
      append-icon="search"
      label="Search"
      single-line
      hide-details
      v-model="search"
    ></v-text-field>
    <v-data-table
      v-model="selected"
      select-all
      item-key="id"
      :headers="headers"
      :items="items"
      :pagination.sync="pagination"
      :total-items="totalItems"
      :loading="loading"
      class="elevation-1"
    >
      <template slot="headers" slot-scope="props">
        <tr>
          <th>
            <v-checkbox
              primary
              hide-details
              @click.native="toggleAll"
              :input-value="props.all"
              :indeterminate="props.indeterminate"
            ></v-checkbox>
          </th>
          <th v-for="header in props.headers" :key="header.text"
            :class="['column sortable', pagination.descending ? 'desc' : 'asc', header.value === pagination.sortBy ? 'active' : '']"
            @click="changeSort(header.value)"
          >
            <v-icon>arrow_upward</v-icon>
            {{ header.text }}
          </th>
        </tr>
      </template>

      <template slot="items" slot-scope="props">
        <tr :active="props.selected" @click="props.selected = !props.selected">
          <td>
            <v-checkbox
              primary
              hide-details
              :input-value="props.selected"
            ></v-checkbox>
          </td>
          <td v-for="(item, key) in props.item" v-if="headers.find(val => val.value === key) ">
            {{ item }}
          </td>

            <v-btn
              color="transparent"
              small
              left
              fab
              light
              style="box-shadow:none"
              @click="edit(props.item)"
              slot="activator"
            >
              <v-icon>edit</v-icon>
            </v-btn>
        </tr>
      </template>
    </v-data-table>
  </div>
</template>

<script>
  import axios from 'axios'
  export default {
    props: ['headers', 'tableFor'],
    data () {
      return {
        search: '',
        totalItems: 0,
        loading: true,
        pagination: {},
        baseUrl: 'http://localhost:8000/api/' + this.tableFor + '/items',
        items: [],
        selected: []
      }
    },
    watch: {
      pagination: {
        handler () {
          this.getDataFromApi('', this.baseUrl)
        }
      }
    },
    mounted () {
      this.getDataFromApi('', this.baseUrl)
    },
    methods: {
      edit (data) {
        this.$emit('onEdit', data)
      },
      toggleAll () {
        if (this.selected.length) this.selected = []
        else this.selected = this.items.slice()
      },
      getDataFromApi (searchval, baseUrl) {
        this.loading = true
        const { sortBy, descending, page, rowsPerPage } = this.pagination
        let ordering = null
        let offset = null
        let searchvalue = null
        if (this.search === '') {
          searchvalue = searchval
        } else {
          searchvalue = this.search
        }
        if (descending) {
          ordering = '-' + sortBy
        } else {
          ordering = sortBy
        }
        offset = (page - 1) * rowsPerPage
        axios({
          method: 'get',
          baseURL: baseUrl + '/?offset=' + offset + '&limit=' + rowsPerPage + '&ordering=' + ordering + '&search=' + searchvalue,
          headers: {
            // Authorization: 'Token bc7ff5323264d2e80afb50a9ec2d93d15bb3c696'
          }
        })
        .then((response) => {
          this.items = response.data.results
          this.totalItems = response.data.count
          if (this.totalItems < offset) {
            this.pagination.page = 1
            this.page = 1
            this.getDataFromApi(searchvalue)
          }
          this.loading = false
        }).bind(this)
        .catch((error) => {
          console.log(error)
          this.loading = false
        })
      }
    }
  }
</script>



