<template>
  <div class="echarts">
    <IEcharts :option="pie" :loading="loading" @ready="onReady" @click="onClick"></IEcharts>
  </div>
</template>

<script type="text/babel">
  import axios from 'axios'
  import IEcharts from 'vue-echarts-v3/src/full.js'
  export default {
    name: 'PieChart',
    components: {
      IEcharts
    },
    props: ['chartFor', 'chartOf', 'chartForUser'],
    data: () => ({
      loading: false,
      pie: {
        title: {
          text: '',
          subtext: '',
          x: 'center'
        },
        tooltip: {
          trigger: 'item',
          formatter: '{a} <br/>{b} : {c} ({d}%)'
        },
        legend: {
          orient: 'vertical',
          left: 'left',
          data: []
        },
        series: [
          {
            type: 'pie',
            radius: '55%',
            center: ['50%', '50%'],
            data: [],
            itemStyle: {
              emphasis: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: 'rgba(0, 0, 0, 0.5)'
              }
            }
          }
        ]
      }
    }),
    mounted () {
      this.pie.title.text = this.chartFor.charAt(0).toUpperCase() + this.chartFor.slice(1) + ' Analysis'
      this.pie.title.subtext = 'for ' + this.chartOf + 's'
      this.getDataFromApi()
    },
    methods: {
      getDataFromApi () {
        axios({
          method: 'get',
          baseURL: 'http://localhost:8000/api/' + this.chartOf + '/' + this.chartFor + '-pi/user/' + this.chartForUser,
          headers: {
            // Authorization: 'Token bc7ff5323264d2e80afb50a9ec2d93d15bb3c696'
          }
        })
        .then((response) => {
          this.pie.legend.data = response.data.legend
          this.pie.series[0].data = response.data.pi
        }).bind(this)
        .catch(() => {
        })
      },
      onReady (instance) {
        console.log(instance)
      },
      onClick (event, instance, echarts) {
        console.log(arguments)
      }
    }
  }
</script>

<style scoped>
  .echarts {
    width: 400px;
    height: 400px;
  }
</style>



